












import { Vue, Component, Prop } from 'vue-property-decorator'

@Component({components: {}})
export default class LineGraph extends Vue {
    @Prop() g_title!: string
    chartsLib: any = null
    chart: any

    @Prop() chartData!: any[]

    get chartOptions () {
        if (!this.chartsLib) return null
        return this.chartsLib.charts.Line.convertOptions({
            chart: {
                title: 'Model Data - ' + this.g_title,
                //subtitle: 'Loss'
            },
            bars: 'vertical', // Required for Material Bar Charts.
            vAxis: {viewWindow:{min: 0, max: null}},
            hAxis: { format: 'decimal' },
            height: 400,
            width: 500,
            colors: ['#1b9e77'],
            annotations: {
                boxStyle: {
                    strokeWidth: 5
                }
            }
        })
    }

    onChartReady (chart: any, google: any) {
        this.chartsLib = google
        this.chart = chart
    }

    public updateChart() {
        this.chart.draw()
    }
}
