import Vue from 'vue'
import Vuex from 'vuex'
import gql from "../lib/gql_lib"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    epochs: {}
  },
  mutations: {
    setEpochs(state, data) {
      state.epochs = data
    },
    updateEpoch(state, data) {
      // @ts-ignore
      if(state.epochs[data.id]) {
        // @ts-ignore
        Vue.set(state.epochs, data.id, Object.assign(state.epochs[data.id], data))
      } else {
        Vue.set(state.epochs, data.id, data)
      }

    }
  },
  actions: {
    async fetchData(store) {
      let result = await gql(
          `query {
                    epochs {
                        id, accuracy, loss, batches {id, accuracy, loss}
                    }
                }`
      )

      let epochs = result.data.data.epochs.reduce((acc: any, room: any) => {
        acc[room["id"]] = room;
        return acc
      }, {})

      store.commit("setEpochs", epochs)
    }
  },
  modules: {
  }
})
