







import { Vue, Component } from 'vue-property-decorator'
import LineGraph from "@/components/LineGraph.vue"

@Component({components: {LineGraph}})
export default class Home extends Vue {
    get acc_data() {
        return [["Epoch", "Accuracy"], ...Object.keys(this.$store.state.epochs).map((epoch) => {
            const acc = this.$store.state.epochs[epoch].accuracy
            const id = this.$store.state.epochs[epoch].id
            return [id, acc]
        })]
    }

    get loss_data() {
        return [["Epoch", "Loss"], ...Object.keys(this.$store.state.epochs).map((epoch) => {
            const loss = this.$store.state.epochs[epoch].loss
            const id = this.$store.state.epochs[epoch].id
            return [id, loss]
        })]
    }
}
